import React from 'react';
import {Appbar, Tooltip} from 'react-native-paper';
import {useAppDispatch, useAppSelector} from '../../../store/hooks';
import {startOnboarding} from '../../../store/slices/upload';
import {FolderType} from '../../../types';
import {
  FolderParams,
  ProjectParams,
  UnifiedStackParams,
} from '../../../types/nav';
import EditorToolBar from '../../editor/editortoolbar';
import {DocumentPicker, writeItem} from '../../../api';
import {setEditingMode} from '../../../store/slices/ui';
import {useClickFile} from '../../../hooks/useClickFile';
import {
  NavigationProp,
  useNavigation,
} from '@react-navigation/native';

export function Actions({
  showFullSearchBar,
  isEncoderProject,
  userId,
}: {
  showFullSearchBar: boolean;
  isEncoderProject: boolean;
  userId?: string;
}) {
  const navigation =
    useNavigation<NavigationProp<UnifiedStackParams>>();
  const dispatch = useAppDispatch();
  const params = useAppSelector(state => state.ui.params);
  const folderType = useAppSelector(state => state.ui.folderType);
  const routeName = useAppSelector(state => state.ui.currentPage);
  const parentRouteName = useAppSelector(
    state => state.ui.parentName,
  );
  const projectId = (params as ProjectParams)?.projectId;
  const folderId = (params as FolderParams)?.folderId;
  const isShop = parentRouteName === 'ShopTab';
  const isEditing =
    useAppSelector(state => state.ui.editingMode) !== undefined;

  let addFileEnabled = [
    FolderType.media,
    FolderType.editor,
    FolderType.encoder,
    FolderType.shop,
  ].includes(folderType);

  let addFolderEnabled = folderType !== FolderType.settings;
  const showScaleControls = routeName === 'EditorProject';

  const newFolderType = 'new_' + folderType;

  if (showScaleControls) {
    addFileEnabled = false;
    addFolderEnabled = false;
  }
  const {onClickFile} = useClickFile();

  const onClickFolder = () => {
    dispatch(setEditingMode('folder'));
  };

  const onClickSearch = () => {
    dispatch(setEditingMode('search'));
  };

  const onClickContact = () => {
    dispatch(setEditingMode('party'));
  };

  const onClickDatabase = () => {
    dispatch(setEditingMode('database'));
  };

  return (
    <>
      {routeName === 'Databases' ? (
        <Appbar.Action
          icon={'database-plus'}
          onPress={onClickDatabase}
        />
      ) : null}
      {addFolderEnabled && !isEditing ? (
        <Tooltip title="Create a folder">
          <Appbar.Action
            icon="folder-plus"
            onPress={() => onClickFolder()}
          />
        </Tooltip>
      ) : null}
      {showScaleControls && (
        <EditorToolBar projectId={projectId} routeParams={params} />
      )}
      {isShop && (
        <Tooltip title="Open webeditor">
          <Appbar.Action
            icon="web"
            onPress={() => navigation.navigate('WebEditor')}
          />
        </Tooltip>
      )}
      {isShop && (
        <Tooltip title="Open shop info">
          <Appbar.Action
            icon="cog"
            onPress={() => navigation.navigate('ShopConfig')}
          />
        </Tooltip>
      )}
      {isShop && (
        <Tooltip title="Open orders">
          <Appbar.Action
            icon="shopping"
            onPress={() =>
              navigation.navigate('Orders', {orderId: undefined})
            }
          />
        </Tooltip>
      )}
      {isShop && (
        <Tooltip title="Open payouts">
          <Appbar.Action
            icon="ethereum"
            onPress={() => navigation.navigate('Payouts')}
          />
        </Tooltip>
      )}
      {!isEditing && folderType === 'contact' ? (
        <Tooltip title="Create a chat">
          <Appbar.Action
            icon="chat-plus"
            onPress={() => onClickContact()}
          />
        </Tooltip>
      ) : null}
      {addFileEnabled && !isEditing ? (
        <Tooltip title="Select a file">
          <Appbar.Action
            icon="file-plus"
            onPress={() =>
              onClickFile(
                folderId || newFolderType,
                isEncoderProject,
                userId as string,
              )
            }
          />
        </Tooltip>
      ) : null}
      {!showFullSearchBar && !isEditing ? (
        <Appbar.Action
          icon="magnify"
          onPress={() => onClickSearch()}
        />
      ) : null}
    </>
  );
}
