import React, {memo} from 'react';
import {ShopStackParams} from '../types/nav';
import Folderpage from '../components/folder/folderpage';
import {FolderType} from '../types';
import FolderLibrary from '../components/folder/folderlibrary';
import {useFolders} from '../hooks/useFolders';
import {ShopProduct} from '../components/shop/product';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import {WebEditor} from '../components/shop/webeditor';
import {ShopConfig} from '../components/shop/config';
import {OrdersPage} from '../components/shop/orders';
import OrderPage from '../components/shop/order';
import {Payouts} from '../components/shop/payout';

const ShopStack = createNativeStackNavigator<ShopStackParams>();
const folderType = FolderType.shop;
const FolderComponent = memo(() => (
  <Folderpage folderType={folderType} />
));
const FolderLibraryComponent = memo(() => (
  <FolderLibrary dataHook={useFolders(folderType, false)} />
));
export const createShopStack = () => {
  const ShopStackScreen = () => {
    return (
      <ShopStack.Navigator
        screenOptions={{
          headerShown: false,
          freezeOnBlur: true,
        }}>
        <ShopStack.Screen
          name="Categories"
          options={{orientation: 'portrait'}}
          component={FolderLibraryComponent}
        />
        <ShopStack.Screen
          name="Product"
          options={{orientation: 'all'}}
          component={ShopProduct}
        />
        <ShopStack.Screen
          name="Folder"
          options={{orientation: 'portrait'}}
          component={FolderComponent}
        />
        <ShopStack.Screen
          name="WebEditor"
          options={{orientation: 'portrait'}}
          component={WebEditor}
        />
        <ShopStack.Screen
          name="ShopConfig"
          options={{orientation: 'portrait'}}
          component={ShopConfig}
        />
        <ShopStack.Screen
          name="Orders"
          options={{orientation: 'portrait'}}
          component={OrdersPage}
        />
        <ShopStack.Screen
          name="Order"
          options={{orientation: 'portrait'}}
          component={OrderPage}
        />
        <ShopStack.Screen
          name="Payouts"
          options={{orientation: 'portrait'}}
          component={Payouts}
        />
      </ShopStack.Navigator>
    );
  };
  return ShopStackScreen;
};
