import React, {memo} from 'react';

import {
  SafeAreaView,
  View,
  useWindowDimensions,
  Platform,
} from 'react-native';

import DragList, {
  DragListRenderItemInfo,
} from 'react-native-draglist';

import {FolderPreviewContentType} from '../../hooks/useFolders';

import ContentItem from './items/contentitem';
import {useFolder} from '../../hooks/useFolder';
import {
  ActivityIndicator,
  IconButton,
  List,
} from 'react-native-paper';
import {useIsFocused, useRoute} from '@react-navigation/native';
import {FolderType} from '../../types';
import {ScrollView} from 'react-native-gesture-handler';
import {getById, getFolderContent, writeItem} from '../../api';
import {FolderParams} from '../../types/nav';
import {FolderNavigator, RenderContentType} from './foldernavigator';
import {styles} from './styles';

export interface FolderLibraryProps {
  folderId?: string;
  folderType: FolderType;
}
const FolderPage = (props: FolderLibraryProps) => {
  const {folderType} = props;
  const isFocused = useIsFocused();
  const {height, width} = useWindowDimensions();
  const isMobile = width > 700 ? false : true;
  const route = useRoute();
  let marginTop = Platform.OS === 'web' ? 0 : 0;
  const params = route.params as FolderParams;
  const {folderId, mnemonic, title, userId} = params;
  const dataHook = useFolder(
    folderId,
    getFolderContent,
    getById,
    folderType,
    writeItem,
    mnemonic,
    title,
    userId,
  );
  const {
    folderContent,
    isLoadingFolder,
    folderContentQuery,
    folder,
    onReordered,
    addMedia,
  } = dataHook;

  let parentFolderId =
    params?.parentFolderId || (folder && folder.folderId) || null;
  if (parentFolderId === folderId) parentFolderId = null;
  const parentFolderHook = useFolder(
    parentFolderId,
    getFolderContent,
    getById,
    folderType,
    writeItem,
  );

  const mstyles = styles;
  mstyles.item.width = width - 80;
  mstyles.scroll.height = height - 65;
  mstyles.scroll.marginTop = marginTop;
  if (!isFocused) return <></>;
  const renderContent = ({
    item: i,
    onDragEnd,
    onDragStart,
  }: DragListRenderItemInfo<FolderPreviewContentType>) => (
    <ContentItem
      i={i}
      key={i.id}
      dataHook={dataHook}
      styles={mstyles}
      width={!isMobile ? (width / 100) * 70 : width}
      onDragStart={onDragStart}
      onDragEnd={onDragEnd}
    />
  );
  return (
    <SafeAreaView>
      <View
        style={{
          height: '100%',
          width: '100%',
          flexDirection: 'row',
        }}>
        {!isMobile ? (
          <FolderNavigator
            parentFolderId={parentFolderId}
            parentFolderHook={parentFolderHook}
            renderContent={renderContent as RenderContentType}
            folderType={folderType}
          />
        ) : null}
        {!isLoadingFolder &&
        !folderContentQuery.isFetching &&
        folderContent !== undefined ? (
          <DragList
            renderItem={renderContent}
            keyExtractor={item => item?.id || item?.date.toString()}
            style={{
              width: '100%',
              height,
              paddingBottom: 50,
            }}
            data={folderContent}
            showsVerticalScrollIndicator={false}
            showsHorizontalScrollIndicator={false}
            scrollEnabled={true}
            ListEmptyComponent={() => (
              <List.Item
                title={'Create your first project'}
                left={() => (
                  <IconButton icon={'creation'} mode="outlined" />
                )}
                style={{padding: 10}}
                onPress={() => addMedia()}
              />
            )}
            onReordered={onReordered}
          />
        ) : (
          <ActivityIndicator size={72} />
        )}
      </View>
    </SafeAreaView>
  );
};

export default memo(FolderPage);
