const createChunks = (file: File) => {
  const cSize = maxSize;
  let startPointer = 0;
  let endPointer = file.size;
  let chunks = [];
  while (startPointer < endPointer) {
    let newStartPointer = startPointer + cSize;
    chunks.push(file.slice(startPointer, newStartPointer));
    startPointer = newStartPointer;
  }
  console.info(
    `total chunks: ${chunks.length} for file ${file.name}`,
  );
  return chunks;
};
export function OrdersPage() {
  const ordersQuery = useQuery('orders', async () => {
    const shopconfig = (await getById(shopId)) as IShopConfig;

    if (shopconfig?.address) {
      const orders = await getOrders(shopconfig.address);
      console.info('orders', orders);
      return orders;
    }

    return [];
  });
  const navigation = useNavigation<NavigationProp<ShopStackParams>>();
  const route = useRoute();
  const params = route.params as OrderParams;
  const orders = ordersQuery.data || [];
  const confirmDelivery = async item => {
    const abiCoder = new ethers.AbiCoder();
    const aesKey = await generateAesKey();
    const encryptedKey = await encryptAesKey(
      aesKey.kp,
      JSON.parse(item.userData.publicKey),
    );
    const uploadEncrypt = async (
      file: File | Blob,
      media: Media,
      partId: number,
    ) => {
      const encryptedFile = await encryptPayloadRaw(
        aesKey.key,
        await file.arrayBuffer(),
      );
      console.info('encryptedFile', encryptedFile);
      const bl = new Blob([encryptedFile.buffer], {
        type: 'text/plain',
      }) as File;
      bl.name = media.filename;
      console.info('bl', bl);
      const fileLink = await pinFileToIPFS(bl);
      return {
        fileLink,
        iv: encryptedFile.iv,
        name: media.filename,
        fileType: media.fileType,
        partId,
      };
    };
    const cartLinks = await Promise.all(
      item.userData.cart.map(async c => {
        const media = (await getById(c.id)) as Media;
        const file = (await getFileReaderOpfs(media)) as File;

        console.info('file', file);

        if (file.size > maxSize) {
          const splits = createChunks(file);
          const links = await Promise.all(
            splits.map((i, index) => uploadEncrypt(i, media, index)),
          );
          return links;
        }

        return await uploadEncrypt(file, media, 0);
      }),
    );
    const {encryptedPayload, iv} = await encryptPayload(
      aesKey.key,
      JSON.stringify(cartLinks),
    );
    const dataBlob = abiCoder.encode(
      ['string'],
      [JSON.stringify({encryptedPayload, iv, encryptedKey})],
    );
    await (await item.contract.confirmDelivery(dataBlob)).wait(1);
    await ordersQuery.refetch();
  };
  return (
    <ScrollView
      style={{width: '100%', backgroundColor: 'black'}}
      showsVerticalScrollIndicator={false}
      showsHorizontalScrollIndicator={false}>
      <FlatList
        data={orders}
        style={{backgroundColor: 'black'}}
        renderItem={props => {
          return (
            <>
              <List.Accordion
                style={{backgroundColor: 'black'}}
                left={lp => (
                  <Text {...lp} style={{...lp.style, fontSize: 10}}>
                    {props.item.state}
                  </Text>
                )}
                key={props.item.id}
                onPress={() => {
                  if (props.item.id === params?.orderId)
                    navigation.navigate('Orders', {
                      orderId: undefined,
                    });
                  else
                    navigation.navigate('Order', {
                      orderId: props.item.id,
                    });
                }}
                right={p => (
                  <IconButton
                    {...p}
                    icon={
                      params?.orderId === props.item.id
                        ? 'chevron-right'
                        : ''
                    }
                  />
                )}
                expanded={params?.orderId === props.item.id}
                title={'ORDER: ' + props.item?.id}
                description={
                  <>
                    <Text style={{fontSize: 12}}>
                      {'Address: ' +
                        props.item.userData?.deliveryAddress +
                        '\nPrice: ' +
                        props.item.price +
                        (props.item.rating > 0
                          ? ' Rating: ' + props.item.rating
                          : ' ')}
                    </Text>
                    {props.item.state === 'Locked' && (
                      <Chip
                        icon={'truck'}
                        onPress={() => confirmDelivery(props.item)}>
                        {'Confirm Delivery'}
                      </Chip>
                    )}
                  </>
                }>
                <></>
              </List.Accordion>
              <Divider />
            </>
          );
        }}
      />
    </ScrollView>
  );
}
